<template>
  <!-- <path :d="computeArc()"
      stroke="#fa3838" :stroke-width="strokeWidth" fill="none"/> -->
  <div class="progress-circular"
      :style="`width: ${size}px; height: ${size}px;`">
    <svg :width=size :height=size :viewBox="`-${size/2} -${size/2} ${size} ${size}`">
      <circle :r="size/2 - strokeWidth/2" cx="0" cy="0" class="progress-circular-stroke transition"
          ref="progressCircle"
          stroke="#cdcdcd" :stroke-width="strokeWidth" fill="none"/>
    </svg>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    value: Number,
    size: Number,    
  },
  watch:{
    value: {
      handler(newVal: number) {
        this.computeStroke();
      }
    }
  }
})
export default class ProgressCircular extends Vue {
  value: number | undefined;  
  
  size: number = 100;
  strokeWidth: number = 4;


  mounted() {
    const progressCircle = this.$refs.progressCircle as SVGCircleElement;
    progressCircle.style.strokeDasharray = `${Math.PI * (this.size - this.strokeWidth)}`;
    progressCircle.style.strokeDashoffset = `${Math.PI * (this.size - this.strokeWidth)}`;
    setTimeout(() => {
      this.computeStroke();      
    }, 0);
  }


  computeStroke(){
    // console.log('computeStroke');
    const progressCircle = this.$refs.progressCircle as SVGCircleElement;
    if (!progressCircle)
      return;
    progressCircle.style.strokeDashoffset = `${Math.PI * (this.size - this.strokeWidth) * (1 - (this.value||0) / 100)}`;
    // console.log(progressCircle.style.strokeDashoffset);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.progress-circular-stroke{
  transition-property: stroke-dashoffset;
  rotate: -90deg;
}

</style>
