<template src="./Home.html"/>
<style src="./Home.scss" lang="scss"/>
  

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import TilesAreaSkill from '@/components/TilesAreaSkill/TilesAreaSkill.vue';
import RadialBarChart from '@/components/RadialBarChart/RadialBarChart.vue';
import ProgressCircular from '@/components/ProgressCircular/ProgressCircular.vue';
import Carousel from '@/components/Carousel/Carousel.vue';
import { helper } from '@/helper';
import { skills } from '@/data';

@Options({
  components: {
    TilesAreaSkill,
    ProgressCircular,
    RadialBarChart,
    Carousel,
  },
})
export default class Home extends Vue {
  years = Math.floor((new Date().getTime() - new Date('2015-05-01').getTime())/(31557600000));
  helper = helper;

  skillData = skills;
  chartSkill?: RadialBarChart;
  carouselSkill?: Carousel;
  skillSelected: string = '';

  chartSkillMounted(chart: RadialBarChart){
    this.chartSkill = chart;
  }
  chartSkillSelected(selection: string){
    this.skillSelected = selection;
    // this.carouselSkill?.goToPage(selection);
  }

  carouselSkillMounted(carousel: Carousel){
    this.carouselSkill = carousel;
  }  

}
</script>
