<template src="./HUD.html"/>
<style src="./HUD.scss" lang="scss"/>
  

<script lang="ts">
import { helper } from '@/helper';
import { Options, Vue } from 'vue-class-component';

@Options({
  components: {
  },
  props: {
  }
})
export default class HUD extends Vue {
  fabThemeClicked = false;
  lampStringPath = "M 60 66 c 0 5 0 10 0 15"
  draggingElement = '';
  dragStart = {x: 0, y: 0};
  dragNow = {x: 0, y: 0};

  FAB_THEME_ICON = {} as HTMLElement;
  FAB_THEME_STRING = {} as HTMLElement;

  mounted(): void {
    window.addEventListener('mousemove', (e) => {this.draggingElement && this.dragging(e.clientX, e.clientY)});
    window.addEventListener('touchmove', (e) => {this.draggingElement && this.dragging(e.touches[0].clientX, e.touches[0].clientY)});
    window.addEventListener('mouseup', (e) => {this.dragStop()});
    window.addEventListener('touchend', (e) => {this.dragStop()});
    this.FAB_THEME_ICON = document.getElementById('fabThemeIcon') as HTMLElement;
    this.FAB_THEME_STRING = document.getElementById('fabThemeString') as HTMLElement;
  }


  showHudAria(){
    const hudAria = (document.getElementById('HUD-aria') as HTMLElement);
    if (!hudAria){
      return;
    }
    if (hudAria.style.display !== 'block'){
      hudAria.style.display = 'block';
    }
  }

  hideHudAria(){
    const hudAria = (document.getElementById('HUD-aria') as HTMLElement);
    if (!hudAria){
      return;
    }
    this.fabThemeClicked = false;
    hudAria.style.display = '';
  }


  dragging(x: number, y: number){
    this.dragNow.x = x;
    this.dragNow.y = y;
    switch(this.draggingElement){
      case 'themeString': {
        if (y > this.dragStart.y){
          const zoom = this.FAB_THEME_ICON.clientWidth/100;
          this.lampStringPath = `M 60 66 c 0 5 0 10 0 ${15 + (y - this.dragStart.y)/zoom}`;
          if (y - this.dragStart.y > 50){
            this.dragStop();            
          }
        }
      } break;
    }
  }

  
  dragStop(){
    switch(this.draggingElement){
      case 'themeString': this.fabThemeAnimation(); break;
    }
    this.draggingElement = '';
  }

  
  onFabThemeClick(){
    this.fabThemeClicked = true;
    this.showHudAria();      
  }

  themeStringDragStart(e: any){
    this.FAB_THEME_STRING.style.transition = '';
    this.FAB_THEME_STRING.style.cursor = 'grabbing';
    this.dragStart = {x: e.clientX || e.touches[0].clientX, y: e.clientY || e.touches[0].clientY};
    this.draggingElement = 'themeString';
  }

  fabThemeAnimation(){
    if (this.dragNow.y - this.dragStart.y > 50){
      helper.theme.change(helper.theme.name === 'dark' ? 'light' : 'dark');
    }
    this.FAB_THEME_STRING.style.transition = '0.2s';
    this.FAB_THEME_STRING.style.cursor = '';
    const pathList = [
      "M 60 66 c -3 6 3 12 0 18",
      "M 60 66 c 3 6 -3 12 0 18",
      "M 60 66 c 0 5 0 10 0 15",
    ]
    let index = 0;
    const rf = () => {
      this.lampStringPath = pathList[index];
      index++;
      if (index < pathList.length){
        setTimeout(rf, 200);
      }
    }
    rf();


    // if (this.fabThemeClicked){
    //   console.log('qua');
    //   this.lampStringPath = "M 60 66 c -3 6 3 12 0 18";
    //   setTimeout(() => {
    //     this.lampStringPath = ;
    //     setTimeout(() => {
    //       this.lampStringPath = ;      
    //     }, 200)
    //   }, 200)
    // }
    // this.lampStringPath = "M 60 66 c -3 6 3 12 0 15";
  }


}
</script>
