<template>
  <div class="db-tile" :style="`height: 100px; width: ${colNumber*20}px; margin-top: 12px;`">
    <div v-for="row in rowNumber" v-bind:key="row">
      <div v-for="col in colNumber" v-bind:key="col"
          class="dot transition" :id="`db_${row}_${col}`"
          :style="{top: ((row-1) * (100/(rowNumber-1))) + '%', left: ((col-1) * (100/(colNumber-1))) + '%'}">
      </div>    
    </div>
  </div>
  
</template>

<script lang="ts">
import { helper } from '@/helper';
import { Options, Vue } from 'vue-class-component';


@Options({
  props: {
    animate: Boolean,
  },
  watch: {
    animate: function(newVal, oldVal){
      if (newVal)
        this.randomThing(this);        
    }
  }
})
export default class DbTile extends Vue {
  animate!: boolean;
  colNumber = 16;
  rowNumber = 5;

  // refElement: HTMLElement | undefined = undefined;


  randomThing = (scope: any, times: number = 0) => {
    // this.refElement = this.refElement || document.getElementsByClassName('db-tile')[0] as HTMLElement;
    // // console.log(this.refElement);
    this.colNumber = helper.device?.type === 'mobile' ? 7 : 16;
    while(times >= 0){
      times--;
      const dot = document.getElementById(`db_${Math.floor(Math.random() * this.rowNumber) + 1}_${Math.floor(Math.random() * this.colNumber) + 1}`);
      if (dot){
        const diameter = Math.floor(Math.random()*20);
        dot.style.margin = `-${diameter/2}px`;
        dot.style.width  = `${diameter}px`;
        dot.style.height = `${diameter}px`;
      }
    }
    if (scope.animate){
      setTimeout(() => {
        scope.randomThing(scope);
      }, 10);
    }
  }

  mounted(){
    this.randomThing(this, this.colNumber * this.rowNumber * 3);
    // this.refElement = document.getElementsByClassName('db-tile')[0] as HTMLElement;
  }

  // updated(){
  //   this.randomThing(this);
  // }
}
</script>


<style scoped lang="scss">

.db-tile{
  width: 100%;
  position: absolute;
  height: 100%;
}

.dot{
  position: absolute;
  background: #fcf9ff;
  width: 10px; height: 10px;
  margin: -5px;
  border-radius: 50%;
  transition-property: width, height, margin;
  transition-duration: 100ms;
}

</style>
