<template src="./TilesAreaSkill.html"/>
<style src="./TilesAreaSkill.scss" lang="scss"/>
  

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import RowSkill from '../RowSkill/RowSkill.vue';
import FeTile from './FeTile.vue';
import BeTile from './BeTile.vue';
import DbTile from './DbTile.vue';
import { helper } from '@/helper';

@Options({
  components: {
    FeTile,
    BeTile,
    DbTile,
    RowSkill,
  },
})
export default class TilesAreaSkill extends Vue {

  selection = 'none';
  over = 'none';


  /**
   * Algoritmo per mostrare o meno la parte animata delle animazioni
   * @param tile 
   */
  showFrontTile(tile: string): boolean {    
    if (helper.device?.type === 'desktop'){
      switch (tile) {
        case 'fe': return ['none','db','mo'].includes(this.selection);
        case 'be': return ['none','fe','db'].includes(this.selection);
        case 'db': return ['none','mo'].includes(this.selection);
        case 'mo': return true;
      }
    }
    return this.selection === 'none';    
  }


  onTileClicked(newSelection: string){
    this.selection = (newSelection !== this.selection) ? newSelection : 'none';
  }

  onMouseOver(newSelection: string){
    this.over = (newSelection !== this.selection) ? newSelection : 'none';
  }

  onMouseLeave(){
    this.over = 'none';
  }
}
</script>
