<template src="./RadialBarChart.html"/>
<style src="./RadialBarChart.scss" lang="scss"/>
  

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import ProgressCircular from '../ProgressCircular/ProgressCircular.vue';

@Options({
  components: {
    ProgressCircular,
  },
  props: {
    data: Object,
    onMount: Function,
    onSelect: Function,
  }
})
export default class RadialBarChart extends Vue {
  data!: any;
  onMount?: (chart: RadialBarChart) => void;;
  positions: {label: string, name: string, value: number, angle: number, height: number, dotX: number, dotY: number, dotR: number}[] = [];
  circles: number[] = [];

  over: number = -1;
  selected: string = '';
  onSelect: ((value: string) => void) | undefined = undefined;


  mounted(): void {
    this.computePositions();
    this.computeCircles();
    this.onMount && this.onMount(this);
  }


  onMouseOver(index: number) {
    setTimeout(() => {      
      this.over = index;
    }, 200);
  }

  onMouseLeave(index: number) {
    if(this.over === index){
      this.over = -1;
    }
  }  
  
  onClick(selection: string){
    this.selected = selection === this.selected ? '' : selection;
    if (this.onSelect){
      this.onSelect(this.selected);
    }
  }


  computePositions(){
    this.positions = this.data.map((d: GridData, i: number) => {
      const angle = (i / this.data.length) * Math.PI*2 + Math.PI;
      const height = d.value * 1.8;
      const dotX = Math.cos(angle + Math.PI/2) * (height+20) + 225 + 20;
      const dotY = Math.sin(angle + Math.PI/2) * (height+20) + 225 - 13;
      const dotR = 500 - dotX - 7;
      return {label: d.label, name: d.name, value: d.value, angle, height, dotX, dotY, dotR};
    });
  }

  computeCircles(){
    this.circles = [];
    this.circles.push(10*36 + 40);
    this.circles.push(8*36 + 40);
    this.circles.push(6*36 + 40);
    this.circles.push(4*36 + 40);
    this.circles.push(2*36 + 40);
  }
}

type GridData = {
  name: string;
  label: string;
  value: number;
};
</script>
