<template src="./Carousel.html"/>
<style src="./Carousel.scss" lang="scss"/>
  

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import { helper } from '@/helper';

@Options({
  components: {
    
  },
  props: {
    selectPage: Object,
    onMount: Function,
  },
  watch:{
    selectPage: {
      handler(newVal: number) {
        this.goToPage(newVal);
      }
    }
  },
})
export default class Carousel extends Vue {
  onMount?: (carousel: Carousel) => void;
  selectPage?: number | string;  // al cambio vado a quella pagina

  pages: {name: string, html: HTMLElement}[] = [];
  pageIndex = 0;
  pageWidth = helper.device?.type === 'mobile' ? document.body.clientWidth - 60 : 400;

  cursorsGap = 20;
  cursorLeft = 0;
  cursorRight = 0;

  touchStartX = 0;
  scrollStartX = 0;

  mounted(): void {
    this.computePages();
    this.computeCursorLeft();
    this.computeCursorRight();
    this.onMount && this.onMount(this);
    setTimeout(() => {
      this.goToPage(this.selectPage || this.pageIndex, true);
    })
  }

  computePages(){
    const domPages = this.$el.children[0]?.children[0]?.children;
    this.pages = [];
    for(let i = 0; i < domPages.length; i++){
      domPages[i].style.width = `${this.pageWidth}px`;
      this.pages.push({
        name: domPages[i].dataset['carouselPage'] || `${i}`,
        html: domPages[i],
      });
    }
    this.pageIndex = Math.max(0, Math.min(this.pageIndex, this.pages.length - 1));
  }


  computeCursorLeft(pageIndex: number = this.pageIndex) {
    this.cursorLeft = pageIndex * (this.cursorsGap + 20);  // 20 cursor width
  }
  computeCursorRight(pageIndex: number = this.pageIndex) {
    this.cursorRight = (this.pages.length - pageIndex - 1) * (this.cursorsGap + 20);  // 20 cursor width
  }

  
  goToPage(page: number | string, ignoreScroll = false) {
    let index = typeof page === 'number' ? page : this.pages.findIndex((p: any) => p.name === page);
    if (index === -1 && typeof page === 'string'){ 
      return;
    }
    index = Math.max(0, Math.min(index, this.pages.length - 1));
    if (index < this.pageIndex){
      this.computeCursorLeft(index);
      setTimeout(() => {
        this.computeCursorRight();
      }, 100);
    }else{
      this.computeCursorRight(index);
      setTimeout(() => {
        this.computeCursorLeft();
      }, 100);
    }
    this.pages[index]?.html.scrollIntoView({behavior: ignoreScroll ? undefined : 'smooth', inline: 'center'});
    this.pageIndex = index;
    this.pages.forEach((page, index) => {
      if (index === this.pageIndex){
        page.html.classList.add('selected');
      }else{
        page.html.classList.remove('selected');
      }
    })
  }


  carouselClicked(event: PointerEvent){
    let cElement : HTMLElement = event.target as HTMLElement;
    while(cElement && !cElement.dataset['carouselPage']){
      cElement = cElement.parentElement as HTMLElement
    }
    if (cElement){
      this.goToPage(cElement.dataset['carouselPage']!);
    }
  }



  touchStart(event: TouchEvent) {
    this.scrollStartX = this.$el.children[0].scrollLeft;
    this.touchStartX = event.touches[0].clientX;
  }
  touchMove(event: TouchEvent) {
    const deltaX = event.touches[0].clientX - this.touchStartX;
    this.$el.children[0].scrollLeft = this.scrollStartX - deltaX;
  }
  touchEnd(event: TouchEvent) {
    const deltaX = event.changedTouches[0].clientX - this.touchStartX;
    if (deltaX > 75){
      this.goToPage(this.pageIndex - 1);
    } else if (deltaX < -75){
      this.goToPage(this.pageIndex + 1);
    } else {
      this.goToPage(this.pageIndex);
    }
  }
}


</script>
